.main-panel {
  min-height: 855px !important;
}

.bt-info {
  color: #0f6ab9;
  background-color: transparent;
  border-color: #0f6ab9;
}

.text-info {
  color: #0f6ab9 !important;
}

.bg-video{
  color: white;
  background-color: #6e7aff;
}

.btn-info, .badge-info {
  background-color: #0f6ab9;
  border-color: #0f6ab9;

  &:hover {
    color: white;
    background-color: #0f6ab9;
  }
}

.bg-blue {
  color: white;
  background-color: #28a745 !important;

  &:hover {
    color: white !important;
  }
}

.btn-outline-info {
  color: #0f6ab9 !important;
  background-color: white;
  background-image: none;
  border-color: #0f6ab9;

  &:hover {
    color: white !important;
    background-color: #0f6ab9;

    i {
      color: white !important;
    }
  }

  i {
    color: #0f6ab9 !important;
    font-size: 14px;
  }
}

.bg-inverse-info li a.active, .nav-pills-info li a.active, .bg-inverse-info li span, .bg-inverse-info li a {
  color: #0f6ab9 !important;
}

.nav-pills-info li a.active {
  border-bottom: 2px solid #0f6ab9 !important;
}

.nav-pills-primary .nav-link.active {
  background-color: #0f6ab9;
}

.input-helper:before {
  background-color: white !important;
}

.dot-opacity-loader span {
  background-color: #0f6ab9 !important;
}