.leagues{
  .MuiTableCell-root{
    img{
      width: 50px;
      height: 50px;
    }
  }
  .error{
    color: red;
  }
  #addLeague, #editLeague{
    .modal-body{
      height: 400px;
      overflow-y: scroll;
    }
  }
  .modal-backdrop.fade.in{
    display: none !important;
  }
  .modal-backdrop.fade.block{
    display: block !important;
  }
}