
#sidebar{
  min-height: 855px !important;
  .header_left{
    width: 100%;
    height: 60px;
    padding: 0 10px;
    border-bottom: 1px solid rgba(129, 125, 161, 0.17);
    span{
      color: white;
      font-size: 18px;
    }

  }
  .user, .admin{
    border-bottom: 1px solid rgba(129, 125, 161, 0.17);
    .title{
      padding: 0 10px;
      height: 30px;
      border-bottom: 1px solid rgba(129, 125, 161, 0.17);
      span{
        font-size: 14px;
        color: white;
      }
    }
  }
  .profile-wrapper{
    .avatar{
      width: 65px;
      height: 65px;
      font-size: 18px;
      color: #3e3c4f;
      border: none;
      background-color: white;
    }
  }

}
